@-webkit-keyframes page-scale-in{
	from{-webkit-transform: scale(0,0);transform: scale(0,0);}
	to{-webkit-transform: scale(1,1);transform: scale(1,1);}
}
@keyframes page-scale-in{
	from{-webkit-transform: scale(0,0);transform: scale(0,0);}
	to{-webkit-transform: scale(1,1);transform: scale(1,1);}
}
@-webkit-keyframes page-move-out{
	from{opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px); }
	to{opacity: 0; -webkit-transform: translateY(-100px); transform: translateY(-100px);}
}
@keyframes page-move-out{
	from{opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px); }
	to{opacity: 0; -webkit-transform: translateY(-100px); transform: translateY(-100px);}
}
.IM-img-update{
	-webkit-transform: scale(0,0);
	    -ms-transform: scale(0,0);
	        transform: scale(0,0);
	position: absolute;
	z-index: 30;
	top: 50%;
	left: 50%;
	width: 710px;
	height: 540px;
	margin-top: -270px;
	margin-left: -355px;
	border:none;
}
		


